.crossfit {
  background-color: $color-default-white;
  margin-bottom: 106px;
  padding-top: 120px;
}

.crossfit__box-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.crossfit__wrapper {
  width: 500px;
  padding: 85px 78px;
  color: $color-default-white;
  background-color: $primary-red;
}

.crossfit__holder {
  display: flex;
  justify-content: space-between;
}

.crossfit__title {
  margin: 0;
  margin-bottom: 28px;
  text-align: left;
  font-family: $ff-alumni;
  font-size: 82px;
  font-weight: 600;
  line-height: 79px;
  letter-spacing: 2.46px;
}

.crossfit-description {
  margin: 0;
  margin-bottom: 45px;
  font-family: $ff-roboto;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.crossfit__lists {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.crossfit__value {
  padding: 0;
  padding-bottom: 13px;
  font-family: $ff-roboto;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.crossfit__text {
  position: relative;
  margin: 0;
  font-family: $ff-roboto;
  font-size: 40px;
  font-weight: 700;
  line-height: 60px;
  border-top: 1px solid $color-default-white;
}

p span {
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}


.crossfit__button {
  margin: 0;
  padding: 25px 95px;
  font-family: $ff-roboto;
  font-size: 12px;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  background-color: transparent;
  border: 4px solid $color-default-white;
}

.crossfit__image {
  display: block;
  width: 660px;
  height: 500px;
  background-color: $primary-red;
}
